@import "../variables/variables.scss";

.liveRecommend {
  width: 100%;
  padding: 0 20px;
}

.title {
  font-size: 17px;
  font-weight: bold;
}

.liveRecommendContainer {
  width: 100%;
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}
